<template>
  <div class="rectangle" v-if="showBlock" @click="stopTimer">
    CLİİİCK CLİCK NOW DUDE
  </div>
</template>

<script>
export default {
    props: ['delay'],
    data(){
        return {
            showBlock: false,
            reactionTime: 0
        }
    },
    mounted(){
        setTimeout(() => {
            this.showBlock = true
            this.startTimer()
        }, this.delay)
    },
    methods: {
        startTimer(){
            this.reactionTime = Date.now()
        },
        stopTimer(){
            this.reactionTime = Date.now() - this.reactionTime
            this.showBlock = false
            this.$emit('endgame', this.reactionTime)
        }
    }
}
</script>

<style>
    .rectangle{
        max-width: 400px;
        background-color: rgb(117, 163, 117);
        text-align: center;
        margin: 40px auto;
        padding: 100px 0;
        border-radius: 20px;
        color: rgb(255, 255, 255);
        font-weight: 900;
        user-select: none;
    }
</style>