<template>
  <h3>Your reaction time is {{reactionTime}}ms</h3>
  <h2 :style="styleRank">{{rank}}</h2>
</template>

<script>
export default {
    props: {
        reactionTime: Number
    },
    data(){
        return{
            styleRank: {color: "red"},
            rank: null,
            color: [
                '#458f78',
                '#e581d8',
                '#7deb72',
                '#5b24ab',
                '#f2d47a',
                '#a8d54f',
                '#e4d7b8',
                '#1ffd78'
            ]
        }
    },
    mounted(){
        this.rank = (
        (this.reactionTime <= 150) ? "HİLE HİLE KESİN HİLE ama adil oynamadın ki şimdi ben ne yapayım" :
        (this.reactionTime <= 200) ? "bordo berelimisin mubarek" :
        (this.reactionTime <= 250) ? "bak bu iyiydi" :
        (this.reactionTime <= 300) ? "tamam buda iyi en azından 300 değil bozma moralini :(" :
        (this.reactionTime <= 350) ? "olmadı tekrar dene bakayım" :
        (this.reactionTime <= 400) ? "noob" :
        (this.reactionTime <= 500) ? "pure noob" : "tamam.")
        this.styleRank = {"color": this.color[Math.round(Math.random() * 8)]}
    }
}
</script>

<style>
    h3{
       background-color: rgb(52, 163, 163); 
       color: black;
    }
</style>