<template>
  <h1>LETS GOOOOO IMA TEST YOUR REACTION</h1>
  <button class="btn-play" @click="startGame" :disabled="isPlaying">Start</button>
  <Block v-if="isPlaying" :delay="delay" @endgame="stopGame" />
  <Results v-if="showResults" :reactionTime="reactedTime" />
</template>

<script>
import Block from './components/Block.vue'
import Results from './components/Results.vue'

export default {
  name: 'App',
  components: {
    Block,
    Results
  },
  data(){
    return {
      isPlaying: false,
      showResults: false,
      delay: null,
      reactedTime: 0
    }
  },
  methods: {
    startGame(){
      this.isPlaying = true
      this.showResults = false
      this.delay = 1000 + Math.random() * 5000
    },
    stopGame(reactedTime){
      this.isPlaying = false
      this.showResults = true
      this.reactedTime = reactedTime
    }
  }
}
</script>

